@import url("https://fonts.googleapis.com/css?family=Montserrat:300,400,500,700|Open+Sans");
@import url(../fontawesome/css/all.css);
/* 
 * Whitespace Reset  */
* {
  padding: 0;
  margin: 0; }

/* 
 * Box-sizing fix */
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

html {
  font: var(--font-size-initial, 12px)/1.5 "Montserrat", sans-serif; }

@media screen and (min-width: 500px) {
  html {
    font-size: var(--font-size-lv2, 14px); } }

@media screen and (min-width: 700px) {
  html {
    font-size: var(--font-size-lv2, 16px); } }

@media screen and (min-width: 1600px) {
  html {
    font-size: var(--font-size-lv4, 18px); } }

body, table, th, td, form, input, textarea, option, select {
  font: inherit; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  padding-bottom: 0.5em;
  font-weight: 400;
  font-family: "Montserrat";
  text-transform: uppercase;
  margin: 1.3em 0; }

h1 {
  font-size: 2.6em; }

h2 {
  font-size: 2.2em; }

h3 {
  font-size: 1.8em; }

h4 {
  font-size: 1.6em; }

h5 {
  font-size: 1.5em; }

h6 {
  font-size: 1.5em; }

p, ul, ol {
  padding-bottom: 1.5em; }

ul {
  padding-left: 2em; }

li {
  list-style-position: inside; }

blockquote {
  background: #003a8f;
  font-style: italic;
  line-height: 33px;
  color: #FFF;
  margin: 1em 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 3em; }
  blockquote p {
    width: 60%;
    text-align: center;
    font-weight: 300; }
  blockquote p:before {
    display: block;
    content: '"';
    font-size: 5rem;
    font-family: serif;
    margin: 0 0.5em;
    color: rgba(231, 231, 231, 0.6); }

table {
  border-collapse: collapse;
  table-layout: fixed;
  width: 100%; }

a {
  color: #757575;
  text-decoration: none;
  -webkit-transition: color 0.3s ease-in-out;
  transition: color 0.3s ease-in-out; }
  a:hover {
    color: #337ab7; }

table, th, td {
  border: 1px solid #CCC; }

tbody {
  vertical-align: baseline; }

th, td {
  padding: 0.5em;
  text-align: left; }

th {
  font-weight: 700;
  color: #666; }

@-webkit-keyframes pop {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1); }
  50% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8); }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1); } }

@keyframes pop {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1); }
  50% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8); }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1); } }

.button {
  border: 1px solid #fff;
  border-radius: 0.2rem;
  padding: 1rem 2.5rem;
  outline: none;
  background: none;
  color: #FFF;
  text-transform: uppercase;
  font-weight: 500;
  -webkit-transition: background 0.2s ease-in, color 0.2s ease-in;
  transition: background 0.2s ease-in, color 0.2s ease-in; }
  .button:hover {
    -webkit-animation: pop 0.3s 1 ease-in forwards;
    animation: pop 0.3s 1 ease-in forwards;
    background: #FFF;
    color: #337ab7; }

.site-header {
  position: absolute;
  width: 100%;
  z-index: 5;
  background: transparent; }

.sticky {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: 2rem 10rem;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap; }
  @media only screen and (max-width: 1260px) {
    .sticky {
      padding: 2rem 1rem; } }
  @media only screen and (max-width: 972px) {
    .sticky {
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
      padding: 2rem 10rem;
      -webkit-box-align: start;
      -ms-flex-align: start;
      align-items: flex-start; } }
  @media only screen and (max-width: 559px) {
    .sticky {
      padding: 2rem 1rem; } }

.site-branding {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  text-transform: uppercase; }

.site-title,
.site-description {
  color: #fff; }

.logo {
  margin-right: 0.5rem; }

.site-title {
  font-weight: bold;
  margin: 0;
  padding: 0;
  font-size: 2rem;
  font-family: 'Open Sans' , serif; }

.site-description {
  margin: 0;
  padding: 0;
  text-align: center;
  letter-spacing: 0.155rem;
  font-size: 0.8rem; }

.navigation {
  display: none; }
  .navigation ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin: 0;
    padding: 0; }
  .navigation li {
    list-style: none outside;
    padding: 1rem;
    padding-bottom: 0;
    position: relative;
    z-index: 1000; }
    .navigation li:before {
      display: block;
      content: '';
      background: transparent;
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      height: 120%;
      border-bottom: 2px solid orange;
      -webkit-transition: width 0.2s ease-in;
      transition: width 0.2s ease-in;
      z-index: 1; }
    .navigation li:hover:before {
      width: 100%;
      z-index: -1; }
    .navigation li a {
      color: #fff;
      font-weight: 400;
      text-transform: uppercase; }
  @media only screen and (max-width: 972px) {
    .navigation ul {
      display: block; } }

#nav-main-toggle {
  display: none; }

.nav-toggle {
  position: absolute;
  top: 3rem;
  right: 20%;
  z-index: 1000;
  font-size: 2rem;
  padding: 0 0.5rem;
  -webkit-animation: navtogglein 0.5s  1 ease-in-out forwards;
  animation: navtogglein 0.5s  1 ease-in-out forwards; }
  .nav-toggle span {
    display: none;
    /* display: inline-block; */ }
  .nav-toggle:before {
    content: '\f0c9';
    color: #FFF; }

@media screen and (min-width: 973px) {
  .nav-toggle {
    display: none; }
  .navigation {
    display: block;
    -ms-flex-item-align: center;
    align-self: center; } }

@media screen and (max-width: 972px) {
  #nav-main-toggle:checked + .site-header .navigation {
    display: block; }
  .navigation {
    position: absolute;
    top: 8rem;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    margin-bottom: 25rem; }
    .navigation li {
      background: #273c75;
      padding-bottom: 1.2rem; }
      .navigation li:before {
        display: none;
        border: none; }
    .navigation a {
      color: #337ab7; } }

/* background */
.banner {
  z-index: 1;
  background-image: url("../images/banner.jpg");
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 12rem; }
  .banner:before {
    display: block;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(21, 73, 149, 0.8);
    z-index: 2; }

.welcoming {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  padding-bottom: 5rem;
  position: relative;
  z-index: 999; }

.trainer {
  margin: 0; }
  .trainer img {
    display: block;
    width: 33rem; }

.intro {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -webkit-box-align: start;
  -ms-flex-align: start;
  align-items: flex-start;
  color: #FFF;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  width: 50rem;
  z-index: 15;
  position: relative; }
  .intro h2 {
    font-weight: 700;
    font-size: 3rem;
    padding-bottom: 1rem;
    margin: 0; }
  .intro span {
    color: orange; }
  .intro p {
    width: 40rem; }

@media screen and (max-width: 972px) {
  #nav-main-toggle:checked + .site-header + .banner {
    padding-top: 30rem; } }
